			searchText: ''  // Ê∑ªÂä†ÊêúÁ¥¢ÊñáÊú¨ÂèòÈáè
		}
	},
	// Âú®onLoadÊñπÊ≥ï‰∏≠Ê∑ªÂä†
	onLoad() {
	    // Ëé∑ÂèñÁä∂ÊÄÅÊ†èÈ´òÂ∫¶
	    try {
	        const systemInfo = uni.getSystemInfoSync();
	        if (systemInfo && systemInfo.statusBarHeight) {
	            this.statusBarHeight = systemInfo.statusBarHeight;
	        }
	        
	        // ÊâìÂç∞ÂΩìÂâçÈ°µÈù¢Ë∑ØÂæÑ
	        const pages = getCurrentPages();
	        const currentPage = pages[pages.length - 1];
	        console.log('ÂΩìÂâçÈ°µÈù¢Ë∑ØÂæÑ:', currentPage.route);
	        console.log('Á≥ªÁªü‰ø°ÊÅØ:', systemInfo);
	    } catch (error) {
	        console.error('Ëé∑ÂèñÁ≥ªÁªü‰ø°ÊÅØÂ§±Ë¥•:', error);
	    }
	}, // ËøôÈáåÁº∫Â∞ë‰∫ÜÈÄóÂè∑
	methods: {
		// Ê∑ªÂä†ÊêúÁ¥¢ÊñπÊ≥ï
		searchCourse() {
