				case 'business': deptId = 2; break;
				case 'arts': deptId = 3; break;
				case 'education': deptId = 4; break;
				default: deptId = 1;
			}
			
			console.log('Ë∑≥ËΩ¨Âà∞Â≠¶Èô¢ËØæÁ®ãÂàóË°®ÔºåÂ≠¶Èô¢ID:', deptId);
			
			// Â∞ùËØïÂ§öÁßçË∑ØÂæÑÊ†ºÂºè
			try {
				// ÊñπÂºè1Ôºö‰ΩøÁî®Áõ∏ÂØπË∑ØÂæÑ
				uni.navigateTo({
					url: './course-list?deptId=' + deptId,
					fail: (err) => {
						console.error('Áõ∏ÂØπË∑ØÂæÑË∑≥ËΩ¨Â§±Ë¥•:', err);
						
						// ÊñπÂºè2Ôºö‰ΩøÁî®ÁªùÂØπË∑ØÂæÑ
						uni.navigateTo({
