									fail: (err3) => {
										console.error('redirectToË∑≥ËΩ¨Â§±Ë¥•:', err3);
										uni.showToast({
											title: 'Ë∑≥ËΩ¨Â§±Ë¥•ÔºåËØ∑Á®çÂêéÂÜçËØï',
